@import '../../utils/variables';
@import '../../utils/mixins';

.text-link {
  width: max-content;
  font-size: 10px;
  font-weight: 2;
  border-bottom: 1px solid #000;
  cursor: pointer;

  &--architecture {
    margin-right: auto;
    margin-top: 8px;
    grid-area: link;
  }

  &--showroom {
    text-align: left;
  }
}
