@import '../../utils/variables';
@import '../../utils/mixins';

.description {
  width: 100%;
  text-align: justify;
  margin-bottom: 16px;

  &--noParagraph {
    text-align: center;

    &::before {
      content: none;
    }
  }

  &--architecture {
    font-size: 14px;
    margin-right: auto;
    margin-bottom: 0;
    max-width: max-content;
  }

  &--shop {
    text-align: center;
    margin-bottom: 12px;
  }
}
